import React, { useRef, useState, memo, Fragment } from 'react';
import FieldRow, { FieldRowV2 } from '../../common/UI/CustomFields/FieldRow/FieldRow';
import { isMobile } from 'react-device-detect';
import classNames from 'classnames';
import MessageContainer from '../ProcessesComps/MessageContainer/MessageContainer';
import 'react-phone-input-2/lib/bootstrap.css';
import { useProcessTaskContext, processTaskAction } from '../../../context/processTask.context';
import ModuleLists from './ModuleLists';
import * as R from 'ramda';
import moment, { now } from 'moment';
import { Button } from '../../common';
import MapView from '../../common/Mapping/MapView';
import { useSelector } from 'react-redux';
import VideoConference from '../../common/VideoConference/VideoConference';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLocationDot } from '@fortawesome/free-solid-svg-icons';
import { computeFormula, formatsQ, generateNewUUID, isJson } from '../../../shared/utility';
import CustomTemplateModuleList from '../../Custom/CustomTemplateModuleList';
import ReactQuill from "react-quill"
import 'react-quill/dist/quill.snow.css'
import cls from './StaticAccordionModule.module.scss';
import clsh from './AccordionModule.module.scss';
import { getProcessById } from '../../../services/processes.service';
import { getComponentById, updateComponentItem } from '../../../services/components.service';
import { first } from 'rxjs';
import { store } from '../../../reduxStore/store';
import { setMessageBar, setProcessModule, setShowMenu } from '../../../reduxStore/actions/flowActions';
import { useNavigate } from 'react-router-dom';


const StaticAccordionModule = props => {
	const { component, blueprintIndex, companyData, components, allComponents, companyUsersArray, selectedComponent, setIsOpen } = props;
	const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();
	const { authenticatedUser } = useSelector(store => store.authReducer);

	const [setActive,] = useState(cls.active);
	const [message] = useState('');
	const content = useRef(null);
	const indexInContextArray = processTaskContext.currentStep?.component_blueprints.findIndex(c => c.id === component.id);

	const hasMap = component.selected_fields.filter(sf => sf.data_type_name === 'Pin' || sf.data_type_name === 'Map').length > 0;
	return (
		<div id="accordion" className={cls.section} key={blueprintIndex} style={{ display: (component.options?.showOnly === undefined || component.options?.showOnly.length == 0 || authenticatedUser.positions.filter(pos => component.options?.showOnly?.includes(pos.id)).length > 0 || component.options?.statusOnly === selectedComponent?.status) ? '' : 'none' }}>
			<div className={classNames(cls.accordionContainer, setActive)} app-variant-has-error={message ? 'error' : ''} >

				{!(hasMap && isMobile) && <p className={cls.cardTitle}>{component?.name}</p>}
				<div ref={content} style={{ maxHeight: setActive === cls.active ? '' : '0px', display: 'flex' }} className={cls.content}>
					<RenderSelectedFields
						components={components}
						allComponents={allComponents}
						component={component}
						handleInputChange={() => { }}
						handlePrefixChange={() => { }}
						handleCurrencyChange={() => { }}
						checkIfAreMandatoryErrored={false}
						handleCheckbox={() => { }}
						isInherited={false}
						indexInContextArray={indexInContextArray}
						handleLocationChange={() => { }}
						processTaskContext={processTaskContext}
						companyData={companyData}
						companyUsersArray={companyUsersArray}
						selectedComponent={selectedComponent}
						setIsOpen={setIsOpen}
					/>
				</div>
			</div>
			{message && message?.errors?.title && <MessageContainer message={message.errors.title} />}
			{message && message?.message && <MessageContainer message={message.message} />}
		</div>
	);
};

const RenderSelectedFields = memo(props => {
	const { component, components, companyData, allComponents, companyUsersArray, selectedComponent, setIsOpen } = props;
	const { allModules, allTasks, companyPositionsArray, map, begunModule } = useSelector(store => store.flowReducer);

	const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();

	const [loading, setLoading] = useState(false)
	const navigate = useNavigate();

	const getComponentsForListing = options => {
		return components
			.filter(component => component.id === options.component_id)
			.map(cmp => {
				return options?.list.reduce((a, b) => {
					return { ...a, [b]: cmp.selected_fields.find(field => field.field_name === b).value }
				}, {})
			});
	};

	const allSelectedFields =
		component?.selected_fields?.length > 0 &&
		R.sortWith([R.ascend(R.prop('index'))])(component?.selected_fields
			?.filter(sf => sf.value !== true || sf?.value !== undefined || (sf?.data_type_name === 'Transaction List')))
			.map((field, index) => {

				let value = selectedComponent.component_fields.find(cf => cf.field_name === field?.field_name && cf.data_type_name === field?.data_type_name)?.value;
				const width = '48%';

				if (field?.options?.list?.length > 0 && field?.options?.component_id?.length > 0 && (field?.data_type_name === 'Step List' || field?.data_type_name === 'Component List')) {
					return (
						<div key={`${field?.id}${index}`} style={{ width: '100%' }}>
							<ModuleLists columns={field.options.list} list={getComponentsForListing(field.options)} />
						</div>
					);
				} else if (field.data_type === 'output' && field.data_type_name !== 'Formula' && field.data_type_name !== 'Item Formula' && field.data_type_name !== 'Record' && field.data_type_name !== 'Filters') {
					return null;
				} else if (field.data_type_name === 'Info') {
					return null;
				} else if (field.data_type_name === 'Transaction List') {
					return null;
				} else if (field.data_type_name === 'URL') {
					return null;
				} else if (field.data_type_name === 'Ratings') {
					return null;
				} else if (field.data_type_name === 'Rewards') {
					return null;
				} else if (field.data_type_name === 'Map') {

					const foundM = JSON.parse(value ? value : (field?.value ?? '{}'));
					if (foundM?.routes?.DRIVE) {
						map.routes = { DRIVE: foundM.routes.DRIVE }
						map.pins = foundM?.pins ?? [];
					} else if (foundM?.pins) {
						map.pins = foundM?.pins;
						map.routes = { DRIVE: [] }

					}
					return <div key={`${field?.id}${index}`} />

				} else if (field.data_type_name === 'Lookup') {
					return null;
				} else if (field.data_type_name === 'Calendar') {
					return null;
				} else if (field.data_type_name === 'Summary') {
					return null;
				} else if (field.data_type_name === 'ID') {

					return (
						<FieldRowV2
							key={`${field?.id}${index}`}
							width={isMobile ? '100%' : width}
							fieldLabel={field?.options?.label ?? field.field_name}
							style={{ border: '0' }}
							fieldIcon={field.icon}
							isProcess
							visible={field.visible ? (selectedComponent?.show || field?.value || value) : false}
							mandatory={false}
							error={field.errors}
							customIconBgColor={'white'}
							enabled={false}
						>
							<input readOnly style={{ pointerEvents: 'none' }} type={field.data_type} onChange={() => { }} autoComplete="none" value={value ?? field?.value} className={cls.fieldTypeInput} name={field.field_name} />
						</FieldRowV2>
					);
				} else {
					const infoField = component?.selected_fields?.find(sf => (sf.data_type_name === 'Info' && sf.visible === true));
					if (infoField) {
						const found = allComponents.find(c => c?.component_fields.filter(cf => cf.component_blueprint_id === infoField.options.component_id && cf.value === value).length > 0);
						if (found) {
							return infoField?.options?.list?.map((label, ix) => {
								const val = found.component_fields.find(cf => cf.field_name === label)?.value ?? '';

								return val?.includes('jpg') || val?.includes('jpeg') || val?.includes('png') ? (
									<FieldRowV2
										width={isMobile ? '100%' : width}
										style={{ border: '0' }}
										key={100 + ix}

										isProcess
										visible={field.visible && selectedComponent?.show}
										mandatory={false}
										error={field.errors}
										fieldLabel={'Additional Information'}
										customIconBgColor={'white'}
									>
										<div className={cls.accordionContainer} onClick={() => window.open(value, '_blank')} style={{ backgroundColor: "transparent", border: 'none' }}>
											<img alt="lg" onError={(e) => e.target.src = 'https://i.quotev.com/b2gtjqawaaaa.jpg'} src={val} style={{ width: '100%', height: '100%', cursor: 'pointer' }} />
										</div>
									</FieldRowV2>
								) : (
									<FieldRowV2
										enabled={false}
										width={isMobile ? '100%' : width}
										key={100 + ix}
										style={{ border: '0' }}
										fieldLabel={label}
										fieldIcon={field.icon}
										isProcess
										visible={field?.value !== undefined ? true : field.visible}
										mandatory={false}
										error={field.errors}>
										{val.length > 55 ? (
											<textarea className={cls.fieldTypeTextarea} type={field.data_type} value={val} onFocus={() => { }} autoComplete="none" onChange={e => { }} readOnly style={{ backgroundColor: 'rgba(234, 231, 231, 0.91)', height: 'auto', padding: '10px' }} />
										) : (
											<input readOnly style={{ pointerEvents: 'none' }} autoComplete="none" value={val} className={cls.fieldTypeInput} name={field.field_name} />
										)}
									</FieldRowV2>
								)
							});
						}
					}
					if (field.data_type_name === 'Pin' && value) {
						const location = JSON.parse(value);
						return (


							<FieldRowV2
								enabled={false}
								width={isMobile ? '100%' : '92%'}
								key={`${field?.id}${index}`}
								style={{ border: '0' }}
								fieldLabel={field?.options?.label ?? field.field_name}
								visible={field?.value !== undefined ? true : field.visible}
								mandatory={false}
								error={field.errors}>
								<div style={{ width: 'inherit', pointerEvents: 'none' }}>
									<MapView
										zoom={field?.options?.zoom}
										mapkey={companyData.misc_info?.map}
										pins={
											location?.realtime
												? location?.destination?.address ? [
													{ ...location.destination, type: 'destination' },
													{ ...location.source, type: 'source' },
													{ ...location.realtime, type: 'realtime' },
												] :
													[
														{ ...location.source, type: 'source' },
														{ ...location.realtime, type: 'realtime' },
													]
												: location?.destination?.address ? [
													{ ...location.destination, type: 'destination' },
													{ ...location.source, type: 'source' },
												] :
													[
														{ ...location.source, type: 'source' },
													]
										}
									/>

									<div className={cls.filtersContainer} style={{ color: 'var(--app-color-gray-dark)', height: 'inherit', width: '100%', display: field?.visible ? '' : 'none' }}>
										<div className={cls.actionsContainer} style={{ fontSize: '12px' }}>

											{location?.destination?.address && <div style={{ width: '100px', fontWeight: 'bold', fontSize: '14px' }}>{'From'}:</div>}
											<div style={{ fontWeight: 'bold' }}>{location.source.address}</div>
											{!location?.destination?.address && <FontAwesomeIcon icon={faLocationDot} style={{ color: 'green', height: '20px', width: '20px' }} />}
										</div>
										{location?.destination?.address && <hr style={{ borderTop: '0.5px solid #D2D2D2' }} />}
										{location?.destination?.address && <div className={cls.actionsContainer} style={{ fontSize: '12px' }}>
											<div style={{ width: '100px', fontWeight: 'bold' }}>{'To'}:</div>
											<div style={{ fontWeight: 'bold' }}>{location?.destination?.address}</div>
										</div>}
									</div>
								</div>
							</FieldRowV2 >


						);
					}
					if (field?.data_type_name === 'Action Button' && field?.options?.action?.length > 0) {

						const usr = companyUsersArray?.find(user => (user.id === value));

						return (
							<FieldRowV2
								width={isMobile ? '100%' : width}
								key={`${field?.id}${index}`} style={{ border: '0' }} isProcess
								fieldLabel={field.options?.notes ? field.options?.notes : (field?.options?.label ?? field.field_name)}
								enabled={false}
								visible={field.visible ? (selectedComponent?.show || field?.value || value) : false} mandatory={false} error={field.errors}>
								{field.options.action === 'user' &&
									<input
										style={{ pointerEvents: 'none' }}
										onChange={() => { }}
										autoComplete="none"
										value={usr ? `${usr?.first_name} ${usr?.last_name}` : (field?.value ?? '')}
										className={cls.fieldTypeInput}
										name={field.field_name}
									/>
								}
							</FieldRowV2>
						);
					}
					if (field.data_type_name === 'Link') {
						return (
							<FieldRowV2
								width={isMobile ? '100%' : width}
								style={{ border: '0' }}
								key={`${field?.id}${index}`}
								fieldIcon={field.icon}
								isProcess
								visible={field.visible ? (selectedComponent?.show || field?.value || value) : false}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
							>
								{value?.includes('jpg') || value?.includes('jpeg') || value?.includes('png') || field?.options?.image ? (

									<img alt="lg" onError={(e) => e.target.src = 'https://i.quotev.com/b2gtjqawaaaa.jpg'} src={value ?? 'https://i.quotev.com/b2gtjqawaaaa.jpg'} style={{ width: '100%', height: '100%', cursor: 'pointer' }} />

								) : value?.includes('pdf') ? (
									<div className={cls.accordionContainer} onClick={() => window.open(value, '_blank')}>
										<embed onClick={() => window.open(value, '_blank')} src={`${value}#toolbar=0&navpanes=0&scrollbar=0`} width="400px" height="500px" />
									</div>
								) : value ? (
									<div style={{ width: 'inherit', textDecoration: 'underline', textAlign: 'center', cursor: 'pointer' }} onClick={() => window.open(value, '_blank')}>
										{'Link'}
									</div>
								) : <div />}
							</FieldRowV2>
						);
					}
					if (field.data_type_name === 'Freehand') {
						return (
							<FieldRowV2
								width={isMobile ? '100%' : width}
								style={{ border: '0' }}
								key={`${field?.id}${index}`}
								fieldIcon={field.icon}
								isProcess
								visible={field?.value !== undefined ? true : field.visible}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
							>
								<div className={cls.accordionContainer} onClick={() => window.open(value, '_blank')}>
									<img alt="lg" onError={(e) => e.target.src = 'https://i.quotev.com/b2gtjqawaaaa.jpg'} src={value ?? 'https://i.quotev.com/b2gtjqawaaaa.jpg'} style={{ width: '100%', height: '100%', cursor: 'pointer' }} />
								</div>
							</FieldRowV2>
						);
					}
					if (field.data_type_name === 'Checkbox') {
						return (
							<FieldRowV2
								width={isMobile ? '100%' : width}
								key={`${field?.id}${index}`}
								style={{ border: '0' }}
								fieldLabel={field.options.value ?? (field?.options?.label ?? field.field_name)}
								fieldIcon={field.icon}
								isProcess
								visible={field.visible ? (selectedComponent?.show || field?.value || value) : false}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
								enabled={false}
							>
								<input style={{ pointerEvents: 'none' }} onChange={() => { }} autoComplete="none" value={value === undefined ? '' : value == true ? 'Yes' : 'No'} className={cls.fieldTypeInput} name={field.field_name} />
							</FieldRowV2>
						);
					}
					if (field.data_type_name === 'YesNo') {
						return (
							<FieldRowV2
								width={isMobile ? '100%' : width}
								key={`${field?.id}${index}`}
								style={{ border: '0.5px dashed gray', padding: '10px' }}
								fieldIcon={field.icon}
								isProcess
								visible={field?.value !== undefined ? true : field.visible}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
								enabled={false}
							>
								<div style={{ display: 'flex', justifyContent: 'space-between', margin: '20px 0px' }}>
									<input id="check-23" type={'checkbox'} readOnly checked={value ? true : false} />
									<label>
										{field.field_name}
									</label>
								</div>
							</FieldRowV2>

						);
					}
					if (field.data_type_name === 'Upload') {
						return (
							<FieldRowV2 key={`${field?.id}${index}`} fieldLabel={field.field_name} fieldIcon={field.icon} isProcess visible={value !== undefined ? true : value?.length > 0} mandatory={false} error={field.errors} customIconBgColor={'white'} style={{ border: '0', marginRight: '10px' }}>
								<a key={`${field?.id}${index}`} href={value} download="Example-PDF-document" target="_blank" rel="noreferrer">
									<Button clear title={'Download File'} />
								</a>
							</FieldRowV2>
						);
					}

					if (field.data_type_name === 'Status') {
						return (
							<FieldRowV2
								width={isMobile ? '100%' : width}
								key={`${field?.id}${index}`}
								style={{ border: '0' }}
								fieldLabel={field?.options?.label ?? field.field_name}
								fieldIcon={field.icon}
								isProcess
								visible={field?.value !== undefined ? true : field.visible}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
								enabled={false}
							>
								<input readOnly style={{ pointerEvents: 'none' }} onChange={() => { }} autoComplete="none" value={selectedComponent?.status ?? (field?.value ?? value ?? '')} className={cls.fieldTypeInput} name={field.field_name} />
							</FieldRowV2>
						);
					}
					if (field.data_type_name === 'Dropdown') {
						if (field.options === 'modules') {
							value = allModules.find(mod => mod.id === value)?.name;
						}
					}
					if (field.data_type_name === 'Positions') {
						value = companyPositionsArray?.find(cp => cp.value === value)?.label;
					}
					if (field.data_type_name === 'People') {
						if (field.options.calendar) {

							let info = {};
							try {
								info = JSON.parse(value ?? (field?.value ?? JSON.stringify({})));
							} catch (err) {
								//
							}
							//const info = JSON.parse(value ?? (field?.value ?? JSON.stringify({})));
							const found = companyUsersArray.find(user => user.id === info?.user);
							value = found ? `${found.first_name} ${found.last_name}` : '';
							if (info?.task) {
								const task = allTasks.find(task => task.id === info.task);
								return <FieldRowV2 enabled={false} width={isMobile ? '100%' : width} key={`${field?.id}${index}`} style={{ border: '0' }}
									fieldLabel={field?.options?.label ?? field.field_name}
									fieldIcon={field.icon} isProcess visible={field.visible ? (selectedComponent?.show || field?.value || value) : false} mandatory={false} error={field.errors}>
									<div style={{ display: 'flex', flexDirection: 'column', width: 'inherit' }}>
										<input
											style={{ pointerEvents: 'none' }}
											autoComplete="none"
											value={value}
											readOnly
											className={cls.fieldTypeInput}
											name={field.field_name}
										/>

										{task && <input
											style={{ pointerEvents: 'none' }}
											autoComplete="none"
											value={task?.name}
											readOnly
											className={cls.fieldTypeInput}
											name={field.field_name}
										/>}
										{task && <input
											style={{ pointerEvents: 'none' }}
											autoComplete="none"
											value={moment(new Date(task?.add_info?.start)).format('DD MMM YYYY h:mm a')}
											readOnly
											className={cls.fieldTypeInput}
											name={field.field_name}
										/>}
										{task?.status && <input
											style={{ pointerEvents: 'none' }}
											autoComplete="none"
											value={task?.status}
											readOnly
											className={cls.fieldTypeInput}
											name={field.field_name}
										/>}
									</div>
								</FieldRowV2>
							}

						} else {

							if (!value) {
								const found = companyUsersArray.find(user => user.id === field.value);
								value = found ? `${found.first_name} ${found.last_name}` : '';

							}


						}
					}
					if (field.data_type_name === 'Video' && selectedComponent?.status === (field?.options?.status ?? 'New')) {

						if ((field?.value || value) && (moment(field?.value ?? value) <= now() && now() < (moment(field?.value ?? value).add('minutes', +(field?.options?.expiry ?? 30))))) {
							return (
								<FieldRowV2 width={'100%'} key={`${field?.id}${index}`} style={{ border: '1px dashed gray' }}
									fieldLabel={field?.options?.label ?? field.field_name}
									fieldIcon={field.icon} isProcess visible={field.visible ? (selectedComponent?.show || field?.value || value) : false} mandatory={false} error={field.errors}>
									<div className={cls.actionsContainer} style={{ display: 'flex', justifyContent: 'center', fontSize: '12px' }}>
										<VideoConference payload={{ name: selectedComponent.id }} />
									</div>
								</FieldRowV2>
							)
						}
					}

					if (field.data_type_name === 'Module' && field?.options?.detail && field?.visible) {
						const module = allModules.find(mod => mod.id === field.options.module_id);
						const transactionsM = R.sortWith([R.descend(R.prop('created_date'))])(allComponents.filter(com => com.module_id === module.id))
							.filter(com => {
								const value = selectedComponent.component_fields.find(cf => cf.field_name === field.options?.key)?.value;
								return com.component_fields.find(cf => cf.value === value && cf.field_name === field.options?.key);
							}).map(com => {
								return { id: com.id, Transaction: com.title, ['Date Created']: `${moment(new Date(com.created_date)).format('DD MMM YYYY h:mm a')}`, ['Status']: com.status }
							});

						return (

							<div className={cls.iconsContainer} style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', alignContent: 'space-between', justifyContent: 'center', width: '100%', margin: '40px 0' }}>
								{!(selectedComponent?.status === (begunModule?.processModule?.module?.options?.lock ?? companyData?.misc_info?.lock) ||
									selectedComponent?.status === (module?.options?.lock ?? companyData?.misc_info?.lock)
								) && <Button
										style={{ backgroundColor: 'var(--app-color-blue)', width: '30%' }}
										title={field?.field_name}
										message={`Preparing ${module.name} entry...`}
										loading={loading}
										clicked={() => {
											setLoading(true);

											getProcessById(module.procedure.id)
												.pipe(first())
												.subscribe({
													next: workflow => {
														if (field?.options?.cmp) {
															const step = workflow?.steps?.find(
																step => step.component_blueprints.filter(cb => cb?.options?.allowMultipleInputs && cb.selected_fields?.find(sf => sf.data_type_name === 'Transaction List')).length > 0
															);
															const cbp = step?.component_blueprints?.find(cb => cb.selected_fields?.filter(sf => sf.data_type_name === 'Transaction List' && sf.visible === true).length > 0)
															const object = cbp?.selected_fields?.filter(sf => sf.options?.mapToItem).reduce((a, b) => {
																const found = R.find(R.propEq('field_name', b.field_name), R.flatten(R.pluck(('selected_fields'), processTaskContext.components.concat(processTaskContext.newStepComponents))));
																if (found && found?.data_type_name === b.data_type_name) {
																	return { ...a, [b.options?.mapToItem]: found.value }
																}
																return a;
															}, {});

															if (Object.values(object).length > 0) {
																getComponentById(field?.options?.cmp)
																	.pipe(first())
																	.subscribe({
																		next: c => {

																			updateComponentItem({ ...object, index: c.component_items?.length, id: generateNewUUID() }, field?.options?.cmp)
																				.pipe(first())
																				.subscribe({
																					next: (item) => {
																						setLoading(false);
																						store.dispatch(setMessageBar({ show: true, message: `Success, an item was added.` }))
																						setIsOpen(false);
																					},
																					error: () => {

																					}
																				});

																		},
																		error: () => {

																		}
																	});

															} else {
																setLoading(false);
															}


														} else {
															setLoading(false);
															processTaskContext.components = [];
															processTaskContext.newStepsComponent = [];
															field.options.fields.filter(f => {
																const value = selectedComponent.component_fields.find(cf => cf.field_name === f)?.value;
																workflow.steps?.forEach(step => {
																	step?.component_blueprints?.filter(cb => !cb.options?.asModuleList).forEach(cb => {
																		const blueprint = cb.selected_fields.find(fld => fld.field_name === f && fld.visible == true);
																		if (blueprint) {
																			blueprint.value = value;
																		}
																	})
																	if (step.component_blueprints.find(cb => cb.selected_fields.filter(sf => sf.field_name === f).length > 0)) {
																		setTaskProcessContext({
																			action: processTaskAction.addNewComponent,
																			value: step.component_blueprints.filter(cb => cb.selected_fields.filter(sf => sf.field_name === f).length > 0),
																		});
																	}
																}
																);
															})

															store.dispatch(
																setProcessModule({
																	...workflow,
																	completeSteps: workflow.steps,
																	steps: workflow.steps,
																	stepIndex: 1,
																	module_id: field.options.module_id,
																	module: allModules.find(mod => mod.id === field.options.module_id),
																})
															);
															setTaskProcessContext({ action: processTaskAction.setCurrentStep, value: workflow.steps[1] });

															setTaskProcessContext({
																action: processTaskAction.setModuleItems,
																value: []
															});

															setIsOpen(false);
															store.dispatch(setShowMenu(false));
															navigate('/begin-module');
														}
													}
												});


										}}
									/>
								}
								{field?.options?.showL && transactionsM?.length > 0 &&
									<FieldRowV2
										fieldLabel={field?.options?.label ?? field.field_name}
										width={'100%'} key={`${field?.id}${index}`} fieldIcon={field.icon} isProcess visible={field.visible} mandatory={false} error={field.errors}>
										<br />
										<CustomTemplateModuleList
											style={{ width: '100%', marginBottom: '20px' }}
											tableHostSimple={isMobile ? cls.itemsContainerMob : clsh.itemsContainer_ii}
											withColumns={false}
											columns={['Transaction', 'Date Created', 'Status',]}
											list={transactionsM}
										/>
									</FieldRowV2>
								}
							</div>

						);
					}

					if (field.data_type_name === 'Module') {
						return null;
					}

					if (field.data_type_name === 'Details' && field?.options?.key && field.options?.fields?.length > 0) {
						const found = allComponents.find(c => c.module_id === field.options.module_id && c.component_fields.find(cf => cf.field_name === field.options.key && cf.value === (selectedComponent?.component_fields?.find(sf => sf.field_name === field.options.key && sf.value)?.value)));
						value = found?.component_fields?.find(cf => cf.field_name === field.options?.fields[0])?.value
					}

					if (field.data_type_name === 'Clock') {
						value = moment(new Date(+value)).format('DD MMM YYYY h:mm a')
					}

					if (field.data_type_name === 'Long Text' && field.options?.rich) {


						return <FieldRowV2 enabled={false}
							width={isMobile ? '100%' : `${+(field?.options?.width ?? '50') > 51 ? '97' : '48'}%`}
							key={`${field?.id}${index}`} style={{ border: '0' }}
							fieldLabel={field?.options?.label ?? field.field_name}
							fieldIcon={field.icon} isProcess
							visible={field.visible ? (selectedComponent?.show || field?.value || value) : false}
							mandatory={false} error={field.errors}>


							<ReactQuill
								modules={
									{ toolbar: false }
								}
								style={{ fontFamily: 'var(--app-text-main-font)' }}
								readOnly={true}
								defaultValue={value}
								theme={"snow"}
							/>
						</FieldRowV2>
					}

					return (
						<FieldRowV2 enabled={false}
							width={isMobile ? '100%' : width}
							key={`${field?.id}${index}`} style={{ border: '0' }}
							fieldLabel={field?.options?.label ?? field.field_name}
							fieldIcon={field.icon} isProcess
							visible={field.visible ? (selectedComponent?.show || field?.value || value) : false}
							mandatory={false} error={field.errors}>
							{
								((value?.length > 50 && !isMobile) || (value?.length > 20 && isMobile && field.data_type_name !== 'Date')) ? (
									<textarea
										readOnly
										className={cls.fieldTypeTextarea}
										style={{ padding: '10px', pointerEvents: 'none' }}
										onFocus={() => { }}
										required={true}
										value={value === null || value === undefined ?
											((field.data_type_name === 'Date' || field.data_type_name === 'Video') ? moment(new Date(field?.value)).format('DD MMM YYYY')
												: field?.value)
											: (field.data_type_name === 'Date' || field.data_type_name === 'Video') ? moment(new Date(value)).format('DD MMM YYYY') : value} onChange={e => { }} />
								) : (
									<input
										readOnly
										style={{ pointerEvents: 'none' }}
										onChange={() => { }}
										autoComplete="none"
										value={value === null || value === undefined ? field?.value : (field.data_type_name === 'Dropdown' && field.options?.type === 'modules') ? allModules.find(mod => mod.id === field.value)?.name : (field.data_type_name === 'Date' || field.data_type_name === 'Video') ? (moment(new Date(value)).format('DD MMM YYYY') === 'Invalid date' ? '' : moment(new Date(value)).format(field?.options?.time ? 'DD MMM YYYY h:mm a' : 'MMMM DD, YYYY')) : value}
										className={cls.fieldTypeInput}
										name={field.field_name}
									/>
								)}
						</FieldRowV2>
					);
				}
			});

	return (
		<div className={cls.emulatedFlexGap} style={{ width: isMobile ? '100%' : '' }}>
			{allSelectedFields}
		</div>
	);
});

export default StaticAccordionModule;

export const StaticAccordionCreator = props => {
	const [showId, setShowId] = useState(false);
	const { component, show = false, process } = props;

	return (
		<div className={cls.accordionContainer} >
			<div className={cls.emulatedFlexGap} style={{ width: isMobile ? '100%' : '100%' }}  >
				<FieldRowV2 width={isMobile ? '100%' : '48%'} fieldLabel={'Created By'} style={{ border: '0' }} isProcess visible={true}>
					<input onClick={() => setShowId(!showId)} readOnly onChange={() => { }} autoComplete="none" value={show ? component.created_by?.id.replace(/\D/g, '').slice(-5) : `${component.created_by?.first_name} ${component.created_by?.last_name}`} className={cls.fieldTypeInput} />
				</FieldRowV2>
				<FieldRowV2 width={isMobile ? '100%' : '48%'} fieldLabel={'Created Date'} style={{ border: '0' }} isProcess visible={true}>
					<input style={{ pointerEvents: 'none' }} onChange={() => { }} autoComplete="none" value={moment(new Date(component.created_date)).format('DD MMM YYYY h:mm a')} className={cls.fieldTypeInput} name={'Created Date'} />
				</FieldRowV2>
				<FieldRowV2 width={isMobile ? '100%' : '48%'} fieldLabel={'Transaction ID'} style={{ border: '0' }} isProcess visible={showId}>
					<input onChange={() => { }} autoComplete="none" value={component.id} className={cls.fieldTypeInput} name={'CID'} />
				</FieldRowV2>
				<FieldRowV2 width={isMobile ? '100%' : '48%'} fieldLabel={'Transaction Title'} style={{ border: '0' }} isProcess visible={showId}>
					<input onChange={() => { }} autoComplete="none" value={component.title} className={cls.fieldTypeInput} name={'CID'} />
				</FieldRowV2>
				<FieldRowV2 width={isMobile ? '100%' : '48%'} fieldLabel={'Module'} style={{ border: '0' }} isProcess visible={showId}>
					<input onChange={() => { }} autoComplete="none" value={process?.module?.name} className={cls.fieldTypeInput} name={'CID'} />
				</FieldRowV2>
				<FieldRowV2 width={isMobile ? '100%' : '48%'} fieldLabel={'Process'} style={{ border: '0' }} isProcess visible={showId}>
					<input onChange={() => { }} autoComplete="none" value={process?.name} className={cls.fieldTypeInput} name={'CID'} />
				</FieldRowV2>
			</div>
		</div>
	);
};

