export const StaticAccordionItems = props => {

	const { field, selectedComponent, companyUsersArray, component } = props;

	const GetInputField = props => {
		const { field, index, item } = props;
		if (field?.data_type_name === 'People' && field?.options?.position_id?.length > 0) {
			const found = companyUsersArray.find(usr => field?.value === `${usr.first_name} ${usr.last_name}`);
			return (
				<div style={{ display: 'flex', justifyContent: 'center' }}>
					{found && <div className={cls.avatarContentContainer}
					>
						<img alt="lg" src={found?.photo} className={cls.avatarUrlImg} />
					</div>}
				</div>

			);

		} else if (field?.data_type_name === 'Link' || field?.data_type_name === 'URL') {
			return (
				field?.value?.includes('jpg') || field?.value?.includes('jpeg') || field?.value?.includes('png') ? <div style={{ display: 'flex', justifyContent: 'center' }}>
					<div className={cls.avatarContentContainer}
					>
						<img alt="lg" src={field?.value} className={cls.avatarUrlImg} />
					</div>
				</div> :
					<div >
						{field?.value}
					</div>

			);

		} else if (field.data_type === 'date' || field?.options?.country_code || field?.options?.currency_type || field.data_type_name === 'Number' || field.data_type_name === 'Clock') {
			return ((field?.options?.display === 'bar') ?
				<div className={cls.fieldTypeInput} style={{ paddingLeft: '0px', border: 'none', height: 'auto' }}>

					<div style={{ height: '10px', width: '100%', backgroundColor: field?.options?.maxC, display: 'flex', flexDirection: 'row', border: 'none' }}

					>
						<input defaultValue={''} className={cls.fieldTypeInput} name={field.field_name}
							style={{ color: 'transparent', border: 'none', height: '10px', width: `${((+(field?.value) / +field.options.max) * 100)}%`, backgroundColor: field?.value ? field?.value === '0' ? 'transparent' : field?.options?.barC : 'transparent' }}
						/>
					</div>


				</div> :
				<div
					style={{
						backgroundColor: field?.options?.valueC ? field?.options?.valueC : '',

					}}>
					{(field?.data_type === 'date' || field?.data_type_name === 'Clock') ? moment(field.value).format(field?.options?.time ? 'DD MMMM YYYY HH:mm' : 'DD MMMM YYYY') : field?.value}
				</div>
			);

		} else if (field?.data_type_name === 'Item Formula' && field?.options?.operator?.length > 0) {

			const value = computeFormula(
				field?.options,
				component.selected_fields.filter(x => field.options.list.includes(x.field_name) && x.options.mapToItem),
				[],
				[],
				item,
				true
			);

			return (
				<div
					style={{
						backgroundColor: field?.options?.valueC ? field?.options?.valueC : '',
					}}>
					{value}
				</div>
			);
		}

		return (
			<div
				style={{
					backgroundColor: field?.options?.valueC ? field?.options?.valueC : '',
				}}>
				{field?.value}
			</div>
		);
	}


	const orderedObj = {
		item_code: null,
		quantity: null,
		value: null,
		alt_value: null,
		total: null,
	};

	const columnsObj = Object.entries(Object.assign(orderedObj, field?.options['map'])).reduce((a, [k, v]) => (v === null ? a : ((a[k] = v), a)), {});

	let footerObj = Object.entries(Object.assign(orderedObj, field?.options['map'])).reduce((a, [k, v]) => (v === null ? a : ((a[k] = v), a)), {});
	Object.keys(columnsObj).map(key => {
		if (key === 'item_code') {
			footerObj.item_code = 'Totals:';
		} else {
			if (key === 'quantity') {
				footerObj[key] = `${R.reduce((a, b) => +a + +b[key] ?? 0, 0, selectedComponent?.component_items ?? [])}`;
			} else if (key === 'alt_value') {
				footerObj[key] = ``;
			} else if (key === 'value') {
				footerObj[key] = ``;
			} else if (key === 'total') {
				footerObj[key] = ``;
			}
		}
	});
	const listHeaders = field?.options?.group ? component?.selected_fields?.find(sf => sf?.options?.mapToItem === field?.options?.groupL && sf.data_type_name === 'Dropdown')?.options?.list ?? [] : field?.options?.type ?
		[field?.options?.type]
		: [undefined];
	const computeValue = (items = [], field, table) => {

		if (items?.length == 0) {
			return 0;
		}

		return items.reduce((a, b) => {

			if (b[field?.options?.mapToItem] === null || b[field?.options?.mapToItem] === '') {
				return a;
			}
			const found = (table?.options?.simple ? JSON.parse(b[field?.options?.mapToItem] ?? '{}')[field?.field_name] : b[field?.options?.mapToItem]) ?? 0;

			if (field?.options?.footer === 'itemsum') {
				return a + +(found === '' ? 0 : `${found}`);
			} else if (field?.options?.footer === 'itemdiff') {
				return a - +(found === '' ? 0 : `${found}`);
			}
			return 0;
		}, 0);

	}
	return (

		<div style={{ width: '100%', border: '1px dashed gray', borderRadius: '10px', padding: '16px', fontFamily: 'var(--app-text-main-font)', margin: '20px 0', display: selectedComponent.component_items.length > 0 ? '' : 'none' }}>

			<p style={{ fontSize: '20px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)' }}>
				{field.field_name}
			</p>

			{listHeaders?.map((li, ix) => {
				const list = R.sortWith([R.ascend(R.prop('index'))])(selectedComponent?.component_items?.filter(it => it[field?.options?.groupL] === li || it.type === li) ?? [])?.map((itm, index) => {
					itm.index = index;
					let object = {
						index: index,

					};
					component?.selected_fields?.filter(sf => sf?.options?.mapToItem).map(fld => {

						const value = isJson(itm[fld.options.mapToItem]) ? JSON.parse(itm[fld.options.mapToItem] ?? '{}')[fld.field_name] : itm[fld.options.mapToItem];
						object[fld.field_name] =
							<GetInputField
								index={index}
								item={itm}
								field={{ ...fld, visible: true, value: value ?? '' }}
							/>
					})
					return object;
				});
				return <>

					{list.length > 0 &&
						<CustomTemplateModuleList
							key={ix}
							withColumns={list.length === 0 ? false : true}
							style={{ width: '100%', marginBottom: '20px' }}
							tableHostSimple={isMobile ? cls.itemsContainerMob : clsh.itemsContainer_ii}
							layeredColumns={R.sortWith([R.ascend(R.prop('index'))])(component?.selected_fields)?.filter(sf => sf?.options?.mapToItem).map(sf => {

								if (sf.options?.group) {
									return { label: sf.options?.group, subColumns: [{ label: sf.field_name }] };
								}
								return { label: sf.field_name };
							}).slice(0, isMobile ? 3 : 8)
							}
							list={list}
							footer={(list.length > 0 && field.options?.footer) ? component?.selected_fields?.filter(sf => sf?.options?.mapToItem).map((sf, idx) => sf?.options?.footer ? computeValue((field?.options?.type ? selectedComponent?.component_items.filter(it => it.type === field?.options?.type) : selectedComponent?.component_items), sf, field) : idx === 0 ? 'Totals:' : '') : []}
						>
							{field?.options?.group &&
								<p style={{ fontSize: '16px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)' }}>
									{li}
								</p>}
						</CustomTemplateModuleList>}

				</>
			}
			)}

		</div>

	);

}
